@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Atkinson+Hyperlegible:ital,wght@0,400;0,700;1,400;1,700&display=swap);
:root{
    --company-color-red:#c11a2b;
    --title-color:rgb(34, 34, 34);
    --info-color:rgb(58, 58, 58);
    --company-color-black:rgb(21, 21, 21);
    --company-color-gray:rgb(155, 155, 155);
    --background-color:#FFF;
}

html,
body{
    height: 100%;
}

body{
    overflow-x:hidden;
    font-family: 'Atkinson Hyperlegible', sans-serif;
}

body.lock{
    overflow-y: hidden;
}

html{
    box-sizing: border-box;
}

main{
    flex: 1 0 auto;
    position: relative;
}

section{
    overflow: hidden;
}

footer{
    flex: 0 0 auto;
}

form{
    position: relative;
}

::-webkit-scrollbar-track{
    background-color: transparent;
    border-radius: 7px;
    padding: 5px;
}

::-webkit-scrollbar-thumb{
    background-color: #c11a2b;
    background-color: var(--company-color-red);
    width: 10px;
    border-radius: 7px;
}

::-webkit-scrollbar{
    background:transparent;
    width: 10px;
}

*,
*::before,
*::after{
    box-sizing: border-box;
    outline: none;
    -webkit-tap-highlight-color: transparent;
}

ul[class],
ol[class]{
    padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd{
    margin: 0;
    font-size: inherit;

}

ul,li[class]{
    list-style: none;
}

img{
    max-width: 100%;
}

input,
button,
textarea,
select{
    font: inherit;
}

button{
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
}

a{
    text-decoration: none;
    color: inherit;
}

iframe{
    border: none;
    width: 100%;
}

.wrapper{
    display: flex;
    flex-direction: column; 
    min-height: 100%;
}

.container{
    max-width: 1300px;
    margin: 0 auto;
    padding: 0 15px;
}

.container-fluid{
    max-width: 1920px;
    margin: 0 auto;
    padding: 0;
}

.inner{
    padding: 100px 0;
}

.list{
    display: flex;
    flex-flow: wrap row;
    margin-top: 15px;
    justify-content: space-between;
}

.title{
    font-size: 48px;
    font-weight: 700;
    margin-bottom: 40px;
    color:rgb(34, 34, 34);
    color:var(--title-color);
    line-height: 50px;
    -webkit-animation: forwards text-appear 0.5s ease-in-out;
            animation: forwards text-appear 0.5s ease-in-out;
    opacity: 0;
}

.sub-title{
    font-size: 17px;
    font-weight: 400;
    line-height: 20px;
    color:rgb(34, 34, 34);
    color:var(--title-color);
}

.info{
    font-size: 16px;
    line-height: 20px;
    color: rgb(58, 58, 58);
    color: var(--info-color);
    -webkit-animation: forwards text-appear 0.5s ease-in-out;
            animation: forwards text-appear 0.5s ease-in-out;
    opacity: 0;
}

.link{
    color: rgb(34, 34, 34);
    color: var(--title-color);
    font-size: 16px;
    border: none;
    letter-spacing: 1px;
    cursor: pointer;
}

.link:hover{
    color: #c11a2b;
    color: var(--company-color-red);
    text-decoration: underline;
    text-underline-offset: 2px;
}

.note{
    color: rgb(34, 34, 34);
    color: var(--title-color);
    font-size: 12px;
    letter-spacing: 2px;
    font-style: italic;
    margin-top: 10px;
}

.content{
    padding: 40px 0;
    text-align: center;
}

.button-first{
    border: none;
    background-color: rgb(21, 21, 21);
    background-color: var(--company-color-black);
    padding: 12px;
    text-transform: uppercase;
    color: #FFF;
    color: var(--background-color);
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    cursor: pointer;
    margin-top: 20px;
}

select{
    border: none;
    border-radius: 5px;
    padding: 12px;
    color: rgb(34, 34, 34);
    color: var(--title-color);
}

option{
    border: none !important;
}

input{
    border: none;
    padding: 12px;
}

input[type="search"]::-webkit-search-cancel-button{
  display: none;
}

.slick-prev:before{
    background-image: url(/static/media/arrow-slider-2.8b99de1c.svg);
    color: unset !important;
}

.slick-next:before {
    background-image: url(/static/media/arrow-slider.adaba527.svg);
    color: unset !important;
}

.slick-disabled{
    display: none !important;
}

.slick-next{
    right: 5px !important;
    z-index: 9;
    opacity: 1 !important;
}

.slick-track{
    margin-left: 0 !important;
    margin-right: 0 !important;
}

.slick-prev{
    left: 5px !important;
    z-index: 9;
    opacity: 1 !important;
}

.centered{
    text-align: center;
}

.center{
    display: flex;
    justify-content: space-between;
    align-items: center;

}

.left{
    width: 45%;
}

.right{
    width: 45%;
}

/* header */

header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 998;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    transition: all 0.3s;
    background: rgba(255, 255, 255, 0.3);
}

header.active{
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    opacity: 1;
}

.header-inner{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 0;
}

.header-menu-list-link{
    text-transform: uppercase;
    letter-spacing: 1px;
}

.header-menu{
    text-align: center;
}

.header-menu-list{
    display: flex;
    align-items: center;
}

.header-menu-list-item{
    margin: 0 30px;
}

.header-menu-list-link{
    line-height: 22px;
}

.header-logo{
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.header-cart img{
    width: 40px;
    height: 40px;
}



.navigation{
    display: none;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    background-color: var(--background-color);
    z-index: 999;
    border-top: 2px solid #000;
}

.navigation-inner{
    padding: 20px 40px;
}

.navigation-menu-list-link img{
    width: 25px;
    height: 25px;
}

.navigation-menu-list-link{
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
}

.header-cart{
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    position: relative;
}

.navigation-menu-list{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header-cart h1{
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    position: absolute;
    top: 60%;
    left: 50%;
    font-size: 14px;
}

/* catalog */

.catalog-categories{
    display: flex;
    align-items: center;
    justify-content: center;
}

.catalog-categorie-button{
    margin-right: 20px;
}

.catalog-categorie-subcategories{
    display: none;
}

.catalog-categorie:hover .catalog-categorie-subcategories{
    display: block;
}

.catalog-categories{
    position: relative;
}

.catalog-categorie-subcategorie{
    margin-bottom: 20px;
}

.catalog-categorie-subcategorie-title{
    margin-bottom: 10px;
}

.catalog-products-selection-clear{
    margin-top: 0 !important;
}

.catalog-categorie-subcategories{
    position: absolute;
    background-color: var(--background-color);
    width: 100%;
    left: 0;
    right: 0;
    border-bottom: 3px solid var(--title-color);
    z-index: 99;
    padding: 0 60px;
}

.catalog-menu{
    display: flex;
    justify-content: space-between;
}

.catalog-menu-categories{
    width: 250px;
}

.catalog-breadcrumbs{
    text-align: center;
    margin-top: 20px;
}

.catalog-menu-categorie-subcategorie-items{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.catalog-menu-categorie-subcategorie{
    margin-bottom: 20px;
}

.catalog-categorie-subcategorie-item{
    margin-right: 15px;
}

.catalog-product-card{
    margin-bottom: 30px;
    position: relative;
    max-width: 24%;
}

.catalog-product-card.active{
    max-width: 32%;
}

.catalog-menu-categorie-subcategorie-item.active{
    color: var(--company-color-red);
}

.catalog-product-card-img-onhover{
    display: none;
    width: 100%;
    height:100%;
}

.catalog-product-card-img-default{
    width: 100%;
    height:100%;
}

.catalog-product-card-top:hover .catalog-product-card-img-onhover{
    display: block;
}

.catalog-product-card-top:hover .catalog-product-card-img-default{
    display: none;
}

.catalog-product-card-img{
    width: 100%;
    height:500px;
}

.catalog-product-card-colors{
    display: flex;
}

.catalog-product-card-link-color{
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin-right: 5px;
    margin-top: 5px;
}

.catalog-product-card-button{
    position: absolute;
    top: 10px;
    right: 10px;
    width: 40px;
    height: 40px;
    background: transparent;
    border: none;
    z-index: 1;
    cursor: pointer;
}

.catalog-product-card-remove-button{
    position: absolute;
    top: 10px;
    right: 10px;
    width: 40px;
    height: 40px;
    background-color: var(--background-color);
    border-radius: 50%;
    border: none;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.catalog-product-card-remove-button svg{
    width: 20px;
    height: 20px;
}

.catalog-product-card-button::before{
    content: '';
    position: absolute;
    width: 40px;
    height: 40px;
    background-repeat: no-repeat;
    background-image: url(/static/media/favourites-transparent.f6fbd0d5.svg);
    background-position: center;
    top: 0;
    left: 0;
}

.catalog-product-card-button:hover::before{
    background-image: url(/static/media/favourites-red.576c921a.svg);
}

.catalog-product-card-button.active::before{
    background-image: url(/static/media/favourites-red.576c921a.svg);
}

.catalog-menu-categories .sub-title{
    font-size: 15px;
}

.catalog-menu-categories .link{
    font-size: 15px;
}

.catalog{
    position: relative;
}

.catalog-products-filter-menu{
    display: none;
}

.catalog-categories-menu-close{
    position: absolute;
    top: 10px;
    right: 10px;
    width: 50px;
    height: 50px;
    display: none;
    background-color: var(--company-color-black);
    justify-content: center;
    align-items: center;
}

.catalog-products-filter{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.catalog-products-information{
    display: flex;
    justify-content: space-between;
}

.catalog-products-pagination{
    text-align: center;
}

.catalog-products-information-products-number{
    margin-right: 10px;
}

.catalog-products-pagination-line{
    position: relative;
    margin-top: 20px;
    height: 3px;
    background-color: var(--company-color-gray);
    width: 100%;
}

.catalog-products-pagination-line-active{
    position: absolute;
    height: 3px;
    background-color: var(--title-color);
    z-index: 999;
}

.catalog-products-pagination-links{
    margin-top: 15px;
    display: flex;
    overflow: auto;
    justify-content: flex-start;
}
.catalog-products-pagination-links::-webkit-scrollbar-thumb{
    background-color: var(--company-color-black) !important;
}

.catalog-products-pagination-link{
    margin-right: 7px;
    margin-bottom: 10px;
    padding: 5px 10px;
    width: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    line-height: 30px;
    cursor: pointer;
    background-color: var(--company-color-gray);
}

.catalog-products-pagination-link button{
    color: var(--background-color);
}

.catalog-products-pagination-link:hover{
    background-color: var(--title-color);
}

.catalog-products-pagination-link.active{
    background-color: var(--title-color);

}

.catalog-navigation{
    display: flex;
    justify-content: center;
}

.catalog-search-query{
    margin-top: 20px;
}

.cart-products-list-empty-button button{
    margin-top: 30px;
}

.catalog-search-bar-button{
    width: 40px;
    height: 20px;
}

.catalog-search-bar-cancel-button{
    width: 40px;
    height: 20px;
}

.catalog-search{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
}

.catalog-search-bar{
    display: flex;
    align-items: center;
    border-bottom: 1px solid var(--company-color-black);
}

.catalog-products-filter{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

/* product */

.product-center{
    display: flex;
    justify-content: space-between;
}

.product-goback-button{
    margin-bottom: 10px;
}

.product-details{
    position: relative;
}

.product-right{
    width: 400px;
}

.product-left{
    width: calc(100% - 450px);
}

.product-gallery{
    display: flex;
}

.product-gallery-img{
    width: 50%;
    height: auto;
}

.product-information-article{
    height: 150px;
    position: relative;
    margin-right: 5px;
}

.product-information-article-img{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
}

.product-information-article-img img{
    width: 100%;
    height: 100%;
}

.product-information-articles-list{
    justify-content: unset !important;
}

.product-information-title{
    margin-bottom: 15px;
}

.product-information-price{
    margin-bottom: 20px;
}

.product-information-info{
    margin-bottom: 20px;
}

.product-information-sex{
    margin-bottom: 10px;
}

.product-details-list{
    display: flex !important;
    flex-flow: row wrap;
}

.product-detail{
    width: 230px;
    padding: 20px;
    background-color: var(--company-color-black);
    margin-bottom: 20px;
    margin-right: 20px;
}

.product-detail .sub-title{
    color: var(--background-color) !important;
    margin-bottom: 5px;
}

.product-detail .info{
    color: var(--company-color-gray);

}

.product-detail-item{
    margin-bottom: 10px;
}

.product-information-articles{
    margin-bottom: 20px;
}

.product-details{
    margin-top: 20px;
}

/* home */

.hero-inner{
    padding: 180px 0 !important;
}

.hero-img{
    position: relative;
}

.hero-img-background{
    position: absolute;
    bottom: -10px;
    right: -15px;
    background-color: var(--company-color-black);
    width: 100%;
    height: 100%;
    z-index: -1;
}

.contactme-pattern{
    position: absolute;
    left: 0;
    top: auto;
    right: 0;
    bottom: 0px;
    z-index: 2;
    overflow: hidden;
    width: 200%;
    height: 28px;
    /* background-image: url('../src/img/5f614fd24a90828a57034936_trangle-top.svg'); */
    background-position: 0 100%;
    background-size: auto;
    background-repeat: repeat-x;
}

.about-list{
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
}

.about-item{
    width: 30%;
}

.about-item-title{
    margin-bottom: 15px;
}

.qa-card{
    width: 100%;
    padding: 40px 20px;
    background-color: var(--company-color-black);
    margin-bottom: 20px;
    cursor: pointer;
}

.qa-card .sub-title,
.qa-card .info{
    color: var(--background-color);
}

.qa-card-answer{
    display: none;
    border-top: 1px solid var(--background-color);
    margin-top: 30px;
    padding: 10px 0;
}

.qa-card.active .qa-card-answer{
    display: block;
}

.qa-card.active .qa-card-question-icon{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

.qa-card-question-icon{
    transition: 0.3s ease-in-out;
}

.qa-card-question-icon img{
    width: 100%;
    height: 100%;
}

.qa-card-top{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
