@media screen and (max-width:1150px){
    .catalog-product-card-img{
        height:400px;
    }
    .product-gallery-img:nth-child(2){
        display: none;
    }
    .product-gallery-img{
        width: 100%;
    }
}

@media screen and (max-width:1040px){
    .catalog-product-card-img-onhover{
        display: none !important;
    }
    .catalog-product-card-img-default{
        display: block !important;
    }
    .catalog-product-card-img{
        height:auto;
    }
    .catalog-product-card.active{
        max-width: 30%;
    }
    .catalog-product-card{
        max-width: 23%;
    }
}

@media(any-hover:none){
    .catalog-product-card-img-onhover{
        display: none !important;
    }
    
    .catalog-product-card-img-default{
        display: block !important;
    }
}

@media screen and (max-width:900px){
    .catalog-categories{
        position: fixed;
        left: -100%;
        background-color: var(--background-color);
        top: 0;
        width: 70%;
        height: 100vh;
        z-index: 999;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 0 20px;
        overflow-y: auto;
        overflow-x: hidden;
        justify-content: flex-start;
        transition: 0.5s ease-in-out;
        border-right: 3px solid var(--company-color-black);
    }
    .product-center{
        flex-direction: column;
    }
    .product-left{
        width: 100%;
    }
    .product-right{
        width: 100%;
    }
    .product-detail{
        width: 100%;
        margin-right: 0;
    }
    .catalog-categories.active .catalog-categories-menu-close{
        display: flex;
    }
    .catalog-categorie:hover .catalog-categorie-subcategories{
        display: none;
    }
    .link{
        font-size: 20px;
        text-decoration: unset !important;
    }
    .catalog-categorie-button{
        padding: 20px 0;
    }
    .catalog-categorie{
        padding: 20px 0;
    }
    .catalog-categories.active{
        left: 0%;
    }
    .catalog-menu{
        flex-direction: column;
    }
    .catalog-menu-categorie-subcategorie-items {
        display: flex;
        flex-direction: row;
    }
    .catalog-menu-categorie-subcategorie{
        display: flex;
    }
    .catalog-menu-categories{
        width: auto;
    }
    .catalog-menu-categorie-subcategories{
        display: flex;
        overflow: auto;
    }
    .catalog-menu-categorie-subcategorie-title{
        white-space: nowrap;
        margin-right: 25px;
        border-radius: 50px;
        background-color: var(--company-color-black);
        padding: 7px 12px;
        color: var(--company-color-gray);
    }
    .catalog-menu-categorie-subcategorie-item.active{
        color: var(--background-color);
        background-color: var(--company-color-gray);
    }
    .catalog-menu-categorie-subcategorie-item{
        margin-right: 25px;
        border-radius: 50px;
        background-color: var(--company-color-black);
        padding: 7px 12px;
        color: var(--background-color);
    }
    .catalog-menu-categorie-subcategorie-item:hover{
        color: var(--background-color);
    }
    .catalog-menu-categorie-subcategorie-item span{
        white-space: nowrap;
    }
    .catalog-products{
        width: 100% !important;
    }
    /* .catalog-product-card{
        margin-left: 15px;
    } */
    .catalog-products-filter-menu{
        display: block;
    }
    .catalog-product-card{
        max-width: 32% !important;
    }
    .catalog-categorie.active .catalog-categorie-subcategories{
        display: flex;
        flex-direction: column;
    }
    .catalog-categorie-subcategories{
        position: unset;
    }
    .catalog-categorie-subcategorie-items{
        display: flex;
        flex-direction: column;
    }
    .catalog-categorie-subcategories{
        padding: 0;
    }
    .catalog-categorie-button{
        width: 100%;
        text-transform: uppercase;
    }
    .catalog-navigation{
        justify-content: space-between;
    }
    .catalog-categorie{
        width: 100%;
        margin-bottom: 25px;
    }
    .catalog-menu-categorie-subcategories::-webkit-scrollbar-thumb{
        background-color: var(--company-color-black) !important;
    }
}

